import React, { Component } from 'react';
import {connect} from 'react-redux';
import '../main.css';
import { BrowserRouter as Router, Route, Switch, Redirect, Link } from "react-router-dom";
import URL from './URL'
import $ from 'jquery'
import Cookies from 'js-cookie'


class Login extends Component {
    constructor(props) {
        super(props)
        this.state = {
            email: '',
            password: '',
            remember: false,
            navigate: '',
            visit: '',
            id:null,
            visitorName:'',
            hidden: true,
        }
        this.handlePasswordChange = this.handlePasswordChange.bind(this);
    this.toggleShow = this.toggleShow.bind(this);
    }
    onSubmit = (v, no) => {
        // event.preventDefault()
        // console.log(v)
        //   console.log(event.target.value)
        switch (no) {
            case 1:
                this.setState({
                    email: v
                })
                console.log('email:', this.state.email)
                break;
            case 2:
                this.setState({
                    password: v
                })
                console.log('password:', this.state.password)

                break;
            case 3:
                this.setState({
                    remember: v
                })
                console.log('remember:', this.state.remember)

                break;

            default:
                break;
        }

    }
    componentDidMount = () => {
        console.log(this.props.location);
        // return false;
        const Visitor = this.props.location
        const visiter = Visitor.state[0].name
        console.log('visitor in login didmount :', visiter)
        this.state.visit=visiter;
        this.setState({
            visit: visiter
        })
       
        if (this.props.password) {
            this.setState({ password: this.props.password });
          }
          this.gotoFragment_auto();
    }
    
    handlePasswordChange(e) {
        this.setState({ password: e.target.value });
      }
    
      toggleShow() {
        this.setState({ hidden: !this.state.hidden });
      }

    gotoFragment = async () => {
        console.log('in fragment api')

        const data = {
            Email: this.state.email,
            Password: this.state.password,
            Remeber: this.state.remember,
        }
        console.log('data:', data)
        if(data.Email=='',data.Password==''){
             $('.msg5').removeClass('d-none');
                     $('.msg5').addClass('d-block');  
                       setTimeout(()=>{
                        $('.msg5').removeClass('d-block');
                        $('.msg5').addClass('d-none'); 
                       } ,2000);
        }
        else{
        if (this.state.visit == 'student') {
            console.log('in login student if:::')
            await fetch(URL.url + 'stu_profile/log_in', {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ data }),
            })
                .then(res => res.json())
                .then((resjson) => {
                    if (resjson.Successful == true) {
                        console.log(this.props.changeGMainData);
                        console.log('Data in response:',resjson.stu_data[0])
                        this.setState({
                            navigate: 'student',
                            id:resjson.stu_data[0].id,
                            visitorName:resjson.stu_data[0].Name
                        })

                        if(this.state.remember==true){
                            Cookies.set('ennroll.com_pass', this.state.password, { expires: 7 });
                            Cookies.set('ennroll.com_email', this.state.email, { expires: 7 });
                        }
                        console.log('in login student if:::updated state:>>', this.state.navigate,'nam',this.state.visitorName)
                        this.doubleClick()
                    }
                    else {
                        
                                    $('.msg1').removeClass('d-none');
                                            $('.msg1').addClass('d-block');  
                                              setTimeout(()=>{
                                               $('.msg1').removeClass('d-block');
                                               $('.msg1').addClass('d-none'); 
                                              } ,2000);                

                        // alert(resjson.Message)

                    }
                })
                .catch(err => {
                    console.log('failed', err)
                    // alert('failed')

                })

        }
        else {

            await fetch(URL.url + 'uni_profile/log_in', {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ data }),
            })
                .then(res => res.json())
                .then((resjson) => {
                    if (resjson.Successful == true) {
                        // alert(resjson.Message)
                        this.setState({
                            navigate: 'university',
                            id:resjson.data[0].id,
                            visitorName:resjson.data[0].name,
                        })
                        this.props.changeGMainData([]);
                        if(this.state.remember==true){
                            Cookies.set('ennroll.com_pass', this.state.password, { expires: 7 });
                            Cookies.set('ennroll.com_email', this.state.email, { expires: 7 });
                        }
                        this.doubleClick()
                    }
                    else {
                        // alert(resjson.Message)
                        $('.msg1').removeClass('d-none');
                                $('.msg1').addClass('d-block');  
                                  setTimeout(()=>{
                                   $('.msg1').removeClass('d-block');
                                   $('.msg1').addClass('d-none'); 
                                  } ,2000);

                    }
                })
                .catch(err => {
                    console.log('failed', err)
                    // alert('failed')

                })

        }
    }
    }
    gotoFragment_auto = async () => {
        console.log('in fragment api')

        const data = {
            Email: Cookies.get('ennroll.com_email'),
            Password: Cookies.get('ennroll.com_pass'),
            Remeber: true,
        }
        console.log('data:', data)
        if(data.Email=='',data.Password==''){
             $('.msg5').removeClass('d-none');
                     $('.msg5').addClass('d-block');  
                       setTimeout(()=>{
                        $('.msg5').removeClass('d-block');
                        $('.msg5').addClass('d-none'); 
                       } ,2000);
        }
        else{
        if (this.state.visit == 'student') {
            console.log('in login student if:::')
            await fetch(URL.url + 'stu_profile/log_in', {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ data }),
            })
                .then(res => res.json())
                .then((resjson) => {
                    if (resjson.Successful == true) {
                        console.log(this.props.changeGMainData);
                        console.log('Data in response:',resjson.stu_data[0])
                        this.setState({
                            navigate: 'student',
                            id:resjson.stu_data[0].id,
                            visitorName:resjson.stu_data[0].Name
                        })
                        console.log('in login student if:::updated state:>>', this.state.navigate,'nam',this.state.visitorName)
                        this.doubleClick()
                    }
                    else {
                       
                                                

                        // alert(resjson.Message)

                    }
                })
                .catch(err => {
                    console.log('failed', err)
                    // alert('failed')

                })

        }
        else {

            await fetch(URL.url + 'uni_profile/log_in', {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ data }),
            })
                .then(res => res.json())
                .then((resjson) => {
                    if (resjson.Successful == true) {
                        // alert(resjson.Message)
                        this.setState({
                            navigate: 'university',
                            id:resjson.data[0].id,
                            visitorName:resjson.data[0].name,
                        })
                        this.props.changeGMainData([]);
                        this.doubleClick()
                    }
                    else {
                        // alert(resjson.Message)

                    }
                })
                .catch(err => {
                    console.log('failed', err)
                    // alert('failed')

                })

        }
    }
    }

    doubleClick = () => {
        console.log('in double click')
        $('#login2').trigger('click');
         window.location.reload(true);
    }
    render() {
        return (
        <div>
             <div class="alert alert-danger d-none  msg5 w-25 alert_custome alert-dismissible fade show">
                        <button type="button" class="close" data-dismiss="alert">&times;</button>
                        <strong>Fillout all failed</strong>.
            </div>
            <div  class="alert alert-danger d-none  msg1 w-25 alert_custome alert-dismissible fade show">
                <button type="button" class="close" data-dismiss="alert">&times;</button>
                <strong>Incorect Email or Password </strong>.
            </div>
            <div className="reg-bg" >
                <div className="container">

                    <div className="row">
                        <div className="col-lg-6 col-md-6 col-sm-12">
                            <div className="content">
                                <h1 className='medium-font'>Welcome to ENNROLL!!!</h1>
                                <p className="h-desc">Let's get to know you a bit so we can start building your Ennroll profile.</p>
                            </div>
                        </div>

                        <div className="col-lg-6 col-md-6 col-sm-12">
                            <div className="reg-box">
                                <img className="reg-box-logo-login" src="../Images/logo.png" />
                                <div className="reg-form loginform light-font">
                                    <form action="" id="loginform" method="post" enctype="multipart/form-data">

                                        <div className="row mt-1">
                                            <div className="col-12">
                                                <input type="text" className="form-control email" value={this.state.email} onChange={(text) => this.onSubmit(text.target.value, 1)} placeholder="E-mail" />
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-11">
                                                <input value={this.state.password} onChange={this.handlePasswordChange}  type={this.state.hidden ? "password" : "text"} className="form-control" name="password" placeholder="Password" required="required"></input>
                                            </div>
                                            <div className="col-lg-1 col-md-1 col-sm-1">
                                                {/* <p className="mt-2" onClick={this.toggleShow} style={{ color: 'gray' }}>show</p>                                            */}
                                                <i className="fa fa-eye mt-3" onClick={this.toggleShow}/>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-12">
                                                <input type="checkbox" value={this.state.remember} onChange={(e) => this.onSubmit(e.target.checked, 3)} />
                                                <label className=" labelu">Remember?</label>
                                            </div>
                                        </div>

                                        <div className="row">
                                            
                                            <div className="col-lg-4 col-md-4 col-sm-4 ">
                                                <Link
                                                    to={{
                                                        pathname: this.state.navigate,
                                                        state:[{id:this.state.id ,name:this.state.visitorName}]
                                                    }}>

                                                    <button id="login2" style={{ color: 'white' }} type="button" onClick={() => this.gotoFragment()} className="btn enrollbtn bold-font">Login</button> </Link>
                                            </div>
                                            <div className="col-lg-4 col-md-4 col-sm-4 ">
                                                

                                                    
                                            </div>
                                             <div className="col-lg-4 col-md-4 col-sm-4  ">
                                                <Link
                                                    to={{
                                                        pathname:"resetpassword",
                                                        state:[{id:this.state.id ,name:this.state.visitorName}]
                                                    }}>

                                                    <p id="login" style={{ color: 'red', "text-align" : 'right' }}   className=" pt-1 ">Forget Password?</p> </Link>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-12 col-md-12 col-sm-12">
                                                <label className=" labelu">By having an account you are agreeing with Enroll's
                                        <a href="terms-of-use.html" className="pl-1">Terms of <br /> Services</a> and <a href="privacy-policy.html">Privacy Policy</a></label>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-lg-12 col-md-12 col-sm-12">
                                                <label className=" labelx">You don't have an account? <Link to="landing"> Sign Up!</Link></label>
                                            </div>

                                        </div>
                                    </form>
                                </div>
                            </div>
                            {/* <!--Reg-Box End--> */}
                        </div>
                        <div className="copyright">
                            <h6>© Copyright 2019 | Ennroll, All Rights Reserved.</h6>
                        </div>
                    </div>
                    {/* <!--Reg-Bg End--> */}

                </div>

            </div>
        </div>
        );
    }
}
const mapStateToProps = (state) => {
     return {
         myG_mainData:state.G_mainData,
         myG_RnkData:state.G_RnkData,
         myG_AcrData:state.G_AcrData,
     }
 }

 const mapDispatchToProps = (dispatch) => {
    return {
        changeGMainData: (data) =>{dispatch({ type:'CHANGE_G_MAIN_DATA',payload:data})},
        changeGUniData: (data) =>{dispatch({ type:'CHANGE_UNI_DATA',payload:data})},
        changeGRnkData: (data) =>{dispatch({ type:'CHANGE_RNK_DATA',payload:data})},
        changeG_P_Data: (data) =>{dispatch({ type:'CHANGE_P_Flag',payload:data})},
        changeG_D_Data: (data) =>{dispatch({ type:'CHANGE_D_Flag',payload:data})},
        changeGAcrData: (data) =>{dispatch({ type:'CHANGE_ACR_DATA',payload:data})}


    }
 }
export default connect(mapStateToProps,mapDispatchToProps)(Login);
