    getValues = (v, no) => {
        // console.log('value:', v, no)
        const name = /^[a-zA-Z ]+$/
        const number = /^[0-9+]+$/
        const email = /^[a-z0-9@._]+$/
        switch (no) {
            case 1:
                if (name.test(v)) {
                    this.setState({
                        uniname: v
                    })
                }
                else if (v.length == 0) {
                    this.setState({
                        uniname: ''
                    })
                }
                console.log('UniName:', this.state.uniname)
                break;
            case 2:
                this.setState({
                    country: v
                })
                $('select[name="dropdown"]').addClass("text-danger");
                console.log('country:', this.state.country)
                break;
            case 3:
                this.setState({
                    city: v
                })
                $('select[name="dropdown1"]').addClass("text-danger");
                console.log('city:', this.state.city)
                break;
            case 4:
                if (email.test(v)) {

                    this.setState({
                        email: v
                    })
                }
                else if (v.length == 0) {
                    this.setState({
                        email: ''
                    })
                }
                console.log('email:', this.state.email)
                break;
            case 5:
                this.setState({
                    termcondition: v
                })
                console.log('termcondition:', this.state.termcondition)
                break;
            case 6:
                this.setState({
                    password: v
                })
                console.log('password:', this.state.password)
                break;
        }
    }
