import React, { Component } from 'react';
import '../main.css';
import {connect} from 'react-redux';
import $ from 'jquery'

class Ranking extends Component {
    constructor(props) {
        super(props);
        this.state = {
            validate_temp_rank:'',
        }
    }
       

    dropRank = (no_rank) => {
      $('.rank'+no_rank+'').remove();
      this.state.no_of_Ranking=this.props.myG_RnkData;
      this.state.no_of_Ranking--;
        this.props.changeGRnkData(this.state.no_of_Ranking);

    }
    validateNumber_rank = (name) => {
      const number = /^[0-9-_.]+$/
      let inp= $('input[name="ranking_'+name+'"]').val();
      let l=inp.length-1;
       // if (name.test(inp)) {
       //      this.setState({
       //         validate_temp_rank:inp,
       //      })
       //      console.log('uniName:', this.state.validate_temp_rank)
       //  }
               if (number.test(inp)) {
                    this.setState({
                        validate_temp_rank:inp,
                    })
                    console.log('uniName:', this.state.uniname)
                }
                else{
                          $('input[name="ranking_'+name+'"]').val("");
                    console.log('uniName:', this.state.uniname)
                }
      // if( inp.codePointAt(l) >= 48 && inp.codePointAt(l) <= 57 ){
      //   this.setState({
      //     validate_temp_rank:inp,
      //   });

      // }else{
      //  $('input[name="ranking_'+name+'"]').val(this.state.validate_temp_rank);
      // }
    }

   
    render() {
        return (
            <div className={'row distroy rank'+this.props._ranking}>
                <div className="col-lg-5 col-md-12 col-sm-12 ">
                    <input type="text" className="form-control graytext" name={"sourcename_"+this.props._ranking }
                        placeholder="Example: QS World University Rankings 2020" required="required" />
                </div>
                <div className="col-lg-2 col-md-12 col-sm-12 ">
                    <input type="text" className="form-control graytext" onKeyUp={ () =>this.validateNumber_rank(this.props._ranking)  } name={"ranking_"+this.props._ranking }
                        placeholder="79th" required="required" />
                </div>
                <div  >
                <button onClick={() => this.dropRank(this.props._ranking)} className="close bg-none text-danger " >&times;</button>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
     return {
         myG_mainData:state.G_mainData,
         myG_RnkData:state.G_RnkData,
         myG_AcrData:state.G_AcrData,
     }
 }

 const mapDispatchToProps = (dispatch) => {
    return {
        changeGMainData: (data) =>{dispatch({ type:'CHANGE_G_MAIN_DATA',payload:data})},
        changeGUniData: (data) =>{dispatch({ type:'CHANGE_UNI_DATA',payload:data})},
        changeGRnkData: (data) =>{dispatch({ type:'CHANGE_RNK_DATA',payload:data})},
        changeGAcrData: (data) =>{dispatch({ type:'CHANGE_ACR_DATA',payload:data})}


    }
 }

export default connect(mapStateToProps,mapDispatchToProps)(Ranking);
