import React, { Component } from 'react';
import '../main.css';
import LandingPage from './2-LandingPage'
import { BrowserRouter as Router, Route, Switch, Redirect, Link } from "react-router-dom";
import URL from './URL'
import Cookies from 'js-cookie'
import $ from 'jquery'


{/* <meta http-equiv="refresh" content="3; url = 2-landingPage.html" /> */}
export default class SplashScreen extends Component{
constructor(props) {
    super(props);

    this.state={
        show:true
    }
}
componentDidMount=()=>{
    setTimeout(() => {

        this.setState({
            show:false
        })
    }, 1500);
    this.gotoFragment_auto();
}
gotoFragment_auto = async () => {
    console.log('in fragment api')

    const data = {
        Email: Cookies.get('ennroll.com_email'),
        Password: Cookies.get('ennroll.com_pass'),
        Remeber: true,
    }
    console.log('data:', data)
    if(data.Email=='',data.Password==''){
         $('.msg5').removeClass('d-none');
                 $('.msg5').addClass('d-block');  
                   setTimeout(()=>{
                    $('.msg5').removeClass('d-block');
                    $('.msg5').addClass('d-none'); 
                   } ,2000);
    }
    else{
    // if (this.state.visit == 'student') {
        console.log('in login student if:::')
        await fetch(URL.url + 'stu_profile/log_in', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ data }),
        })
            .then(res => res.json())
            .then((resjson) => {
                if (resjson.Successful == true) {
                    console.log(this.props.changeGMainData);
                    console.log('Data in response:',resjson.stu_data[0])
                    this.setState({
                        navigate: 'student',
                        id:resjson.stu_data[0].id,
                        visitorName:resjson.stu_data[0].Name
                    })
                    console.log('in login student if:::updated state:>>', this.state.navigate,'nam',this.state.visitorName)
                    this.doubleClick()
                }
                else {
                   
                       this.uni_login_auto();
                     

                    // alert(resjson.Message)

                }
            })
            .catch(err => {
                console.log('failed', err)
                // alert('failed')

            })
      }
}
    // }
    // else {
    uni_login_auto = async () => {
      const data = {
        Email: Cookies.get('ennroll.com_email'),
        Password: Cookies.get('ennroll.com_pass'),
        Remeber: true,
    }
        await fetch(URL.url + 'uni_profile/log_in', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ data }),
        })
            .then(res => res.json())
            .then((resjson) => {
                if (resjson.Successful == true) {
                    // alert(resjson.Message)
                    this.setState({
                        navigate: 'university',
                        id:resjson.data[0].id,
                        visitorName:resjson.data[0].name,
                    })
                    this.props.changeGMainData([]);
                    this.doubleClick()
                }
                else {
                    // alert(resjson.Message)

                }
            })
            .catch(err => {
                console.log('failed', err)
                // alert('failed')

            })

    // }
  }

 doubleClick = () => {
        console.log('in double click')
        $('#login2').trigger('click');
         window.location.reload(true);
    }
    render(){
    return ( 
        <div>
            {this.state.show==true?
              <div className="splash-bg">
              <div className="container">
                  <div className="row splashcontent pt-5 mt-5">
                      <div className="col-lg-12 col-md-12 col-sm-12 pt-5 mt-5">
                          <h6>Stay Tuned for a powerful AI and Machine Learning Application to help you with your Higher education Quest!</h6>
                      </div>
                      <div className="col-lg-12 col-md-12 col-sm-12 pt-4 mt-4">
                          <h6>To find out more email us at:<br/> <a className="redbold">info@studentrecruitmentalliance.com</a></h6>
                      </div>
                  </div>
              </div>
          </div>
            :
            <LandingPage></LandingPage>
            }
            <Link
                to={{
                    pathname: this.state.navigate,
                    state:[{id:this.state.id ,name:this.state.visitorName}]
                }}>

                <button id="login2" style={{ visibility: 'hidden' }} type="button" onClick={() => this.gotoFragment()} className="btn enrollbtn bold-font">Login</button> </Link>
            
        </div>
       
    );
}}
