import React, { Component } from 'react';
import '../main.css';
import $ from 'jquery'
import Sidebar from './studentSidebar'
import uniProfile from '../Images/unilogo.png'
import feeIcon from '../Images/tutionfeeW.png'
import { Link } from 'react-router-dom';
import URL from './URL'
class Recommendations extends Component {
    constructor(props) {
        super(props);
        this.state = {
            uniProfile: uniProfile,
            feeIcon: feeIcon,
            avgTutoin: '12,212',
            acceptRate: '50%',
            uniName: 'Massachusetts Institute of Technology',
            address: 'NY, United States',
            unidata: ['a', 'b', 'c'],
            universities: [],
            universities2: [
                { abc: 'abc' },
            ],
            firstname: '',
            uniID: null,
            flag: true
        }
    }

    componentDidMount = () => {
        console.log('In recommendations')
        const Visitor = this.props.location
        const visiterid = Visitor.state[0].id
        const visiterName = Visitor.state[0].name
        console.log('Recco. student logged in  with id:', visiterid, 'and Name:', visiterName, 'Navigate:', this.props.location.state[0].navigate)

        this.setState({
            StutudentID: visiterid,
            firstname: visiterName
        })

        console.log('updated in recom ', this.state.StutudentID, this.state.firstname)
        this.RecommendationsAPI()
    }
    RecommendationsAPI = async () => {
        console.log('in API')
        await fetch(URL.url + 'uni_detail/uni_profile_details', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({}),
        })
            .then(res => res.json())
            .then((resjson) => {
                //  alert(resjson.Message)
                console.log('unies', resjson.uni_data[0])
                this.setState({
                    universities: resjson.uni_data,
                })
                console.log('unies>>>:', this.state.universities[0].id)
            })
            .catch(err => {
                console.log('failed', err)
                alert('failed')

            })
    }
    render() {
        return (
            <body>
                <Sidebar name={this.props.location.state[0].name} id={this.props.location.state[0].id} navigate={this.props.location.state[0].navigate} />
                <main role="main" className="col-md-9  col-lg-10 " style={{ float: 'right' }}>
                    {/* <!-- search-bar ----------------------------------------------------------------- --> */}
                    <div className="row items-center ">
                        <div className=" col-12 displayinline">
                            <i className="fa fa-search pr-1 mt-2"></i>
                            <input className="form-control form-control-dark border-0 light-font" type="text" placeholder="Search here..." aria-label="Search" />
                            <i className="fa fa-bell mt-2" aria-hidden="true"></i>
                        </div>
                    </div>

                    {/* <!-- search-bar end----------------------------------------------------------------- --> */}

                    <div className="row mt-2">
                        <div className="col-lg-12 pl-4 pr-4 pt-4">
                            <div className="row items-center pl-4 pr-4">
                                <h5 className="black lightfont under-line">Your Wishlist</h5>
                            </div>

                            {this.state.flag == false ? <div className="row pl-4 pr-4 ">
                                <p className=" black lightfont ">Sorry, you do not have any Wishlist.</p>
                            </div> : <div className="mt-4">
                                    {this.state.universities2.map((data, index) => (

                                        <div className="row">

                                            <div className="col-lg-4 col-md-12 col-sm-12 mt-2">
                                                <div className="bground ">
                                                    <div className="row m-0">
                                                        <div className="col-10">
                                                        </div>
                                                        <div className="col-1">
                                                            <i className="fa fa-times pl-3 pt-1 "></i>
                                                        </div>
                                                    </div>
                                                    <div className="row recom m-0">
                                                        <div className="col-4">
                                                            <img src={uniProfile} className="rounded-circle" alt="img" />
                                                        </div>
                                                        <div className="col-1">
                                                            <img src={feeIcon} className="feeIcon mt-4" alt="img" />
                                                        </div>
                                                        <div className="col-3 mt-3 p-0 Average ">
                                                            <p className="mb-0">Average Tution</p>
                                                            <div className="displayinline">
                                                                <p>USD</p>
                                                                <p className="pl-1">{data.abc}</p>
                                                            </div>
                                                        </div>
                                                        <div className="col-1">
                                                            <img src="../Images/rateW.png" className="starIcon mt-4" alt="img" />
                                                        </div>
                                                        <div className="col-3 mt-3 p-0 Average ">
                                                            <p className="mb-0">Acceptance Rate</p>
                                                            <div className="displayinline">
                                                                <p className="pl-1">{data.abc} </p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="row mt-2 m-0">
                                                        <div className="col-12">
                                                            <p className="uniNames" style={{ width: '65%' }}> {data.abc}</p>
                                                        </div>
                                                    </div>
                                                    <div className="row mt-2 recom m-0">
                                                        <div className="col-4 mb-3">
                                                            {/* <Link
   to={{
    pathname: "uniprofile",
    state:[{id:this.props.location.state[0].id ,name:this.props.location.state[0].name}]
}}>

   <button type="button" className="btn interestedbtn">View</button></Link> */}
                                                        </div>
                                                        <div className="col-2">
                                                        </div>
                                                        <div className="col-6 text-end">
                                                            <p>{data.abc},{data.abc}</p>

                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    ))
                                    }
                                </div>}






                            <div className="row items-center margintopminus pl-4 pr-4">
                                <p className="black">Add schools to your wishlist from the recommendations below</p>
                            </div>
                            <div className="row pl-4 pr-4 mt-4">
                                <h5 className="lightfont black under-line">Our Recommendations:</h5>
                            </div>
                            <div className="row mt-2">
                                <div className="col-lg-12 col-md-12 col-sm-12 pl-4">
                                    <p className="lightfont black">Based on your profile and defined line of budget, we have picked a few recommendations for you select your programs of interest and add them to your bucket!</p>
                                </div>
                            </div>

                        </div>
                    </div>

                    <div className="row mt-2 ml-2 mr-2">
                        {this.state.universities.map((data, index) => (

                            console.log('dataa>>:', data.id),
                            <div className="col-lg-4 col-md-12 col-sm-12 mt-4">
                                <div className="bground ">
                                    <div className="row m-0">
                                        <div className="col-10">
                                        </div>
                                        <div className="col-1">
                                            <i className="fa fa-times pl-3 pt-1 "></i>
                                        </div>
                                    </div>
                                    <div className="row recom m-0">
                                        <div className="col-4">
                                            <img src={uniProfile} className="rounded-circle" alt="img" />
                                        </div>
                                        <div className="col-1">
                                            <img src={feeIcon} className="feeIcon mt-4" alt="img" />
                                        </div>
                                        <div className="col-3  mt-3 p-0 Average ">
                                            <p className="mb-0">Average Tution</p>
                                            <div className="displayinline">
                                                <p>USD</p>
                                                <p className="pl-1">{data.u_fact_rankings[0].averge_tution}</p>
                                            </div>
                                        </div>
                                        <div className="col-1">
                                            <img src="../Images/rateW.png" className="starIcon mt-4" alt="img" />
                                        </div>
                                        <div className="col-3 mt-3 p-0 Average ">
                                            <p className="mb-0">Acceptance Rate</p>
                                            <div className="displayinline">
                                                <p className="pl-1">{data.u_fact_rankings[0].acceptance_rate} </p>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row mt-2 m-0">
                                        <div className="col-12">
                                            <p className="uniNames" style={{ width: '65%' }}> {data.u_introductions[0].name}</p>
                                        </div>
                                    </div>
                                    <div className="row mt-2 recom m-0">
                                        <div className="col-4 mb-3">
                                            <Link
                                                to={{
                                                    pathname: "uniprofile",
                                                    state: [{ id: this.props.location.state[0].id, name: this.props.location.state[0].name }]
                                                }}>

                                                <button type="button" className="btn interestedbtn">View</button></Link>
                                        </div>
                                        <div className="col-2">
                                        </div>
                                        <div className="col-6 text-end">
                                            <p>{data.u_introductions[0].country_id},{data.u_introductions[0].city_id}</p>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}

                    </div>

                    <div className="row">
                        <div className="col-lg-10 col-md-12 col-sm-12">
                        </div>

                        
                    </div>

                </main>
            </body>
        );
    }
}

export default Recommendations;