import React, { Component } from 'react';
import '../main.css';
import $ from 'jquery'
import Sidebar from './uniSidebar'
import URL from './URL'

class SettingsStudent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            studentName:'Harvard University',
            oldpass: '',
            newpass: '',
            confirmpass: '',
        }
    }
    // getvalue = (t) => {
    //     // let v=$('input[name="oldpass"]').val();
    // }
    componentDidMount = () => {

        const Visitor = this.props.location
        const visiterid = Visitor.state[0].id
        const visiterName = Visitor.state[0].name
        console.log('student logged in with id:', visiterid, 'and Name:', visiterName)

    }
    GetValues(val, no) {
        switch (no) {
            case 1:
                this.setState({
                    oldpass: val
                })
                console.log('oldPass:', this.state.oldpass)
                break;
            case 2:
                this.setState({
                    newpass: val
                })
                console.log('newPass:', this.state.newpass)
                break;
            case 3:
                this.setState({
                    confirmpass: val
                })
                console.log('confirmPass:', this.state.confirmpass)
                break;
        }

    }

    paassword = async () => {
        console.log(this.state.newpass.length)
        if(this.state.newpass.length==6){
        if(this.state.newpass==this.state.confirmpass){
          let data = {
              uni_id: this.props.location.state[0].id,
              new_password:this.state.newpass,
              old_password:this.state.oldpass
          }
          console.log(data)
          await fetch(URL.url+'setting/reset_password1', {
              method: 'POST',
              headers: {
                  Accept: 'application/json',
                  'Content-Type': 'application/json',
              },
              body: JSON.stringify({ data }),
          })
              .then(res => res.json())
              .then((resjson) => {
               alert(resjson.Message)
                 
              })
              .catch(err => {
                  console.log('failed', err)
                //  alert('failed')
        
              })
        }
        else{
    alert('Your new password and confirm password must be same')
        }
        }
        else{
            alert('Your password length must be 6')
 
        }
    }

    render() {
        return (
            <body>
                <Sidebar name={this.props.location.state[0].name} id={this.props.location.state[0].id} />
                <main role="main" className="col-md-9  col-lg-10" style={{ float: 'right' }}>

                    <div className="row items-center ">
                        <div className=" col-12 displayinline">
                            <i className="fa fa-search pr-1 mt-2"></i>
                            <input className="form-control form-control-dark border-0 light-font" type="text" placeholder="Search here..." aria-label="Search" />
                            <i className="fa fa-bell mt-2" aria-hidden="true"></i>
                        </div>
                    </div>

                    {/* <!-- search-bar end----------------------------------------------------------------- --> */}

                    <div className="row mt-2">
                        <div className="col-lg-12 bg-light p-4">
                            <div className="row items-center pl-4 pr-4">
                                <h3 className="black">Welcome, </h3>
                                 <h3 className="redbold under-line ml-2">{this.props.location.state[0].name}</h3>
                            </div>
                            <div className="mb-4"></div>
                        </div>
                    </div>


                    <div className="row">
                        <div className="col-lg-12 col-md-12 col-sm-12 persoanl-info-form">
                            {/* <!-- Settings  --> */}
                            <div className="row bg-light pl-3 pr-3">
                                <div className="col-lg-12 col-md-12 col-sm-12">
                                    <h5 className="lightfont black under-line2">Settings:</h5>
                                </div>
                            </div>
                            <div className="row mt-4 pl-3 pr-3">
                                <div className="col-lg-12 col-md-12 col-sm-12">
                                    <h5 className="lightfont black under-line2">Update Password:</h5>
                                </div>
                            </div>

                            <div className="row mt-3 pl-3 pr-3">
                                <div className="col-lg-4 col-md-12 col-sm-12">
                                    <input type="text" className="form-control" value={this.state.oldpass} onChange={(e) => this.GetValues(e.target.value, 1)} placeholder="Old Password" />
                                </div>
                                <div className="col-lg-4 col-md-12 col-sm-12">
                                    <input type="text" className="form-control" value={this.state.newpass} onChange={(a) => this.GetValues(a.target.value, 2)} placeholder="New Password" />
                                </div>
                                <div className="col-lg-4 col-md-12 col-sm-12">
                                    <input type="text" className="form-control" value={this.state.confirmpass} onChange={(a) => this.GetValues(a.target.value, 3)} placeholder="Confirm New Password" />
                                </div>
                            </div>
                           
                            <div className="row mb-5 text-center">
                                <div className="col-lg-10 col-md-12 col-sm-12">
                                </div>

                                <div className="col-lg-2 col-md-12 col-sm-12">
                                    <button type="button" onClick={() => this.paassword()} className="btn round-btn2">Save</button>
                                </div>
                            </div>
                            {/* <!-- Settings end --> */}

                        </div>
                    </div>
                </main>
            </body>

        );
    }
}

export default SettingsStudent;