import React, { Component } from 'react';
import '../main.css';
import $ from 'jquery'

class Program extends Component {
    constructor(props) {
        super(props);
    }

    validateAlphabet = (name) => {
      let inp= $('input[name="'+name+'"]').val();
      let l=inp.length-1;
      if( inp.codePointAt(l) >= 65 && inp.codePointAt(l) <= 90 || inp.codePointAt(l) >= 97 && inp.codePointAt(l) <= 122 ){

      }else{
        $('input[name="'+name+'"]').val('');
      }
    }

     dropProgram = (f,d,p) => {
          // alert(f+'_'+d+'_'+p);
          $('.program_class_'+f+'_'+d+'_'+p).parent().remove();
    }
    render() {
        return (
               <div  className={"program_class_"+this.props.no_f+"_"+this.props.no_d+"_"+this.props.no_p} >
                   <div className="row">
                       <div className="col-lg-4 col-md-12 col-sm-12 displayinline">
                           <p className="pr-2">Programs Offered:<p>(Under these Departments)</p></p>
                       </div>
                       <div className="col-5">
                       </div>
                       <div className="col-lg-3 col-md-12 col-sm-12">
                           <button onClick={ () => this.dropProgram(this.props.no_f,this.props.no_d,this.props.no_p) } className="btn addbtn edit_hide "><i className="fa fa-minus pr-2"></i>Drop This Program</button>
                       </div>
                   </div>
                   <div className="row">
                       <div id="program13" className="col-lg-3 col-md-12 col-sm-12 displayinline">
                           <input type="text" className="form-control graytext" name={"prog_"+this.props.no_f+"_"+this.props.no_d+"_"+this.props.no_p} placeholder="Option 1" required="required" />
                           <span className="must3">*</span>
                       </div>
                   </div>
               </div>
            );
    }
}

export default Program;