import { BrowserRouter as Router, Route, Switch, Redirect, Link } from "react-router-dom";
import React, { Component } from 'react';
import DatePicker from 'react-date-picker'
import Cookies from 'js-cookie'


import 'primereact/resources/themes/nova-light/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';

import { Calendar } from 'primereact/calendar';
import '../main.css';
import $ from 'jquery';
import '../loader'
import URL from './URL'
export default class StudentForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            visitor: '',
            mr: '',
            firstname: '',
            lastname: '',
            othername: '',
            password: '',
            dob:new Date(2000, 0, 1) ,
            mdob:'',
            pob: '',
            gender: '',
            email: '',
            mob: '',
            Cresidence: '',
            phonecode:'',
            termsChecked: false,
            loginpath: '',
            countries: [],
            hidden : true,
        }
        this.handlePasswordChange = this.handlePasswordChange.bind(this);
    this.toggleShow = this.toggleShow.bind(this);
  
    }

    

  handlePasswordChange(e) {
    this.setState({ password: e.target.value });
  }

  toggleShow() {
    this.setState({ hidden: !this.state.hidden });
  }

    componentDidMount=()=>{
        this.get_countries()
        if (this.props.password) {
            this.setState({ password: this.props.password });
          }
    }
    PhandleChange = date => {
         var v=date;
        v=v.toLocaleDateString();
        var b = v;
         // alert(v);
         var b = v;
        this.state.dob=v;
         v = new Date(v)
         this.state.dob=v;
         this.state.mdob=b;
         // alert(b);
        this.setState({
          mdob: b,
          dob:v
        });
    // alert(this.state.dob);
    // alert(this.state.mdob);
  };
    gotoLogIn = async () => {
        var data = {
            Mr: this.state.mr,
            Name: this.state.firstname,
            Last_name: this.state.lastname,
            Other_name: this.state.othername,
            DOB: this.state.mdob,
            POB: this.state.pob,
            Phone: this.state.mob,
            Gender: this.state.gender,
            Email: this.state.email,
            Residence_country_id: this.state.Cresidence,
            phonecode:this.state.phonecode,
            password: this.state.password,

        }
        console.log(data)
        //  URL='http://192.168.100.6:80/'
        //console.log(URL.URL)
        // return false;
        if (data.Mr == '' || data.Name == '' || data.Last_name == '' ||  data.DOB == '' || data.POB == '' || data.Gender == '' || data.Email == '' && data.Residence_country_id == '' || data.password == ''  ) {
            $('.msg2').removeClass('d-none');
                    $('.msg2').addClass('d-block');  
                       setTimeout(()=>{
                        
                        $('.msg2').removeClass('d-block');
                        $('.msg2').addClass('d-none'); 
                       } ,2000);
        }
        else {
            
            await fetch(URL.url + 'stu_profile/stu_profile_register1', {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ data }),

            })
                .then(res => res.json())
                .then((resjson) => {
                    // this.Loginn()
                    // alert(resjson.data);
                    if(resjson.data!=112 ){
                    this.setState({
                        loginpath: 'login'
                    })
                    console.log('loginPAaath:', this.state.loginpath)
                    $('.msg1').removeClass('d-none');
                    $('.msg1').addClass('d-block');  
                       setTimeout(()=>{
                        
                        $('.msg1').removeClass('d-block');
                        $('.msg1').addClass('d-none'); 
                        this.doubleClick()
                       } ,2000);
                    }
                    else{
                        $('.msg3').removeClass('d-none');
                        $('.msg3').addClass('d-block');  
                           setTimeout(()=>{
                            
                            $('.msg3').removeClass('d-block');
                            $('.msg3').addClass('d-none'); 
                            // this.doubleClick()
                           } ,9000);
                    }
                })
                .catch(err => {
                    console.log('failed', err)

                    // alert(err.Message);

                })
        }

    }
    gotoFragment = async () => {
        console.log('in fragment api')

        const data = {
            Email: this.state.email,
            Password: this.state.password,
            Remeber: true,
        }
        console.log('data:', data)
        if(data.Email=='',data.Password==''){
             $('.msg5').removeClass('d-none');
                     $('.msg5').addClass('d-block');  
                       setTimeout(()=>{
                        $('.msg5').removeClass('d-block');
                        $('.msg5').addClass('d-none'); 
                       } ,2000);
        }
        else{

            console.log('in login student if:::')
            await fetch(URL.url + 'stu_profile/log_in', {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ data }),
            })
                .then(res => res.json())
                .then((resjson) => {
                    if (resjson.Successful == true) {
                        console.log(this.props.changeGMainData);
                        console.log('Data in response:',resjson.stu_data[0])
                        this.setState({
                            navigate: 'student',
                            id:resjson.stu_data[0].id,
                            visitorName:resjson.stu_data[0].Name
                        })

                        if(this.state.remember==true){
                            Cookies.set('ennroll.com_pass', this.state.password, { expires: 7 });
                            Cookies.set('ennroll.com_email', this.state.email, { expires: 7 });
                        }
                        console.log('in login student if:::updated state:>>', this.state.navigate,'nam',this.state.visitorName)
                        this.doubleClick()
                    }
                    else {
                        
                                    $('.msg1').removeClass('d-none');
                                            $('.msg1').addClass('d-block');  
                                              setTimeout(()=>{
                                               $('.msg1').removeClass('d-block');
                                               $('.msg1').addClass('d-none'); 
                                              } ,2000);                

                        // alert(resjson.Message)

                    }
                })
                .catch(err => {
                    console.log('failed', err)
                    // alert('failed')

                })

        

            await fetch(URL.url + 'uni_profile/log_in', {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ data }),
            })
                .then(res => res.json())
                .then((resjson) => {
                    if (resjson.Successful == true) {
                        // alert(resjson.Message)
                        this.setState({
                            navigate: 'university',
                            id:resjson.data[0].id,
                            visitorName:resjson.data[0].name,
                        })
                        this.props.changeGMainData([]);
                        if(this.state.remember==true){
                            Cookies.set('ennroll.com_pass', this.state.password, { expires: 7 });
                            Cookies.set('ennroll.com_email', this.state.email, { expires: 7 });
                        }
                        this.doubleClick()
                    }
                    else {
                        // alert(resjson.Message)
                        $('.msg1').removeClass('d-none');
                                $('.msg1').addClass('d-block');  
                                  setTimeout(()=>{
                                   $('.msg1').removeClass('d-block');
                                   $('.msg1').addClass('d-none'); 
                                  } ,2000);

                    }
                })
                .catch(err => {
                    console.log('failed', err)
                    // alert('failed')

                })

        
    }
    }

    getValues = (v, no) => {
        // console.log('value:', v, no)
        const name = /^[a-zA-Z ]+$/
        const number = /^[0-9+]+$/
        const email = /^[a-z0-9@._]+$/

        switch (no) {
            case 1:
                    this.setState({
                        mr: v
                    })
                    console.log('mr:', this.state.mr)
                break;
            case 2:
                if (name.test(v)) {

                    this.setState({
                        firstname: v
                    })
                }
                else if (v.length == 0) {
                    this.setState({
                        firstname: ''
                    })
                }

                console.log('FirstName:', this.state.firstname)
                break;
            case 3:
                if (name.test(v)) {
                    this.setState({
                        lastname: v
                    })
                }
                else if (v.length == 0) {
                    this.setState({
                        lastname: ''
                    })
                }

                console.log('LastName:', this.state.lastname)
                break;
            case 4:
                if (name.test(v)) {
                    this.setState({
                        othername: v
                    })
                }
                else if (v.length == 0) {
                    this.setState({
                        othername: ''
                    })
                }

                console.log('otherName:', this.state.othername)
                break;
            case 5:
                this.setState({
                    dob: v
                })


                console.log('DAteofBirth:', this.state.dob)
                break;

            case 6:
                if (name.test(v)) {

                    this.setState({
                        pob: v
                    })
                }
                else if (v.length == 0) {
                    this.setState({
                        pob: ''
                    })
                }
                console.log('PlaceofBirth:', this.state.pob)
                break;
            case 7:
                this.setState({
                    gender: v
                })
                console.log('gender:', this.state.gender)
                break;
            case 8:
                if (email.test(v)) {
                    this.setState({
                        email: v
                    })
                }
                else {
                    this.setState({
                        email: ''
                    })
                }
                console.log('email:', this.state.email)
                break;
            case 9:

                if (number.test(v)) {
                    this.setState({
                        mob: v
                    })
                }
                else {
                    this.setState({
                        mob: ''
                    })
                }
                console.log('mobileno:', this.state.mob)
                break;
            case 10:
                this.setState({
                    Cresidence: v
                })
                // if(this.state.phonecode==0){
                    this.state.phonecode=v;
                // }
                console.log('Cresidence:', this.state.Cresidence)
                break;
            case 11:
                this.setState({
                    termsChecked: v
                })
                console.log('termsChecked:', this.state.termsChecked)
                break;
            case 12:
                this.setState({
                    password: v
                })
                console.log('password:', this.state.password)
                break;
            case 13:
                this.setState({
                    phonecode: v
                })
                console.log('phonecode:', this.state.phonecode)
                break;
            default:
                break;
        }
    }

    Loginn = () => {
        // alert(window.location.origin)
        var url = window.location.origin + '/login?name=student&id=1'
        window.location.replace(url)

    }
    doubleClick() {
        console.log('in double click')
        $('#login2').trigger('click');
    }


    get_countries = async () => {

        let data = {

        }
        console.log('get countries is calling')
        await fetch(URL.url + 'stu_profile/country_data', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ data }),
        })
            .then(res => res.json())
            .then((resjson) => {
                //   alert(resjson.Message)
                //    console.log(resjson.c_data)
                this.setState({
                    countries: resjson.c_data
                })
                console.log(this.state.countries)

            })
            .catch(err => {
                console.log('failed', err)
                // alert('failed')

            })
    }


    render() {
        return (
        <div>
             <div  class="alert alert-success d-none  msg1 w-25 alert_custome alert-dismissible fade show">
                <button type="button" class="close" data-dismiss="alert">&times;</button>
                <strong>Save Successfully!</strong>.
            </div>
            <div  class="alert alert-warning d-none  msg2 w-25 alert_custome alert-dismissible fade show">
                <button type="button" class="close" data-dismiss="alert">&times;</button>
                <strong>Fillout all failed!</strong>.
            </div>
            <div  class="alert alert-warning d-none  msg3 w-25 alert_custome alert-dismissible fade show">
                <button type="button" class="close" data-dismiss="alert">&times;</button>
                <strong>Email or Password already used!</strong>.
            </div>
            <div className="reg-bg">
                <div className="container">

                    <div className="row">
                        <div className="col-lg-6 col-md-6 col-sm-12">
                            <div className="content">
                                <h1 className='medium-font'>Welcome to ENNROLL!!!</h1>
                                <p className="h-desc">Let's get to know you a bit so we can start<br /> building your Ennroll profile.</p>
                            </div>
                        </div>

                        <div className="col-lg-6 col-md-6 col-sm-12">
                            <div className="reg-box light-font">
                                <img className="reg-box-logo" src="../Images/logo.png" />
                                <div className="reg-form">
                                    <form action="" method="post" enctype="multipart/form-data">
                                        <div className="row mt-1">
                                            <div className="col-lg-2 col-md-12 col-sm-12">
                                                <select name="dropdown" onChange={(e) => this.getValues(e.target.value, 1)}>
                                                <option value="" disabled selected>Select</option>
                                                    <option value="Mr">Mr.</option>
                                                    <option value="Mrs">Mrs.</option>
                                                    <option value="Ms">Ms.</option>
                                                    <option value="Other">Other</option>
                                                </select>
                                            </div>
                                            <div className="col-lg-3 col-md-12 col-sm-12">
                                                <input value={this.state.firstname} onChange={(e) => this.getValues(e.target.value, 2)} type="text" className="form-control" name="firstname" placeholder="First Name" required="required" />
                                            </div>
                                            <div className="col-lg-3 col-md-12 col-sm-12">
                                                <input value={this.state.lastname} onChange={(e) => this.getValues(e.target.value, 3)} type="text" className="form-control" name="lastname" placeholder="Middle Name" required="required" />
                                            </div>
                                            <div className="col-lg-4 col-md-12 col-sm-12">
                                                <input value={this.state.othername} onChange={(e) => this.getValues(e.target.value, 4)} type="text" className="form-control" name="lastname" placeholder="Surname" required="" />
                                            </div>
                                        </div>

                                        <div className="row">
                                            <div className="col-lg-4 col-md-4 col-sm-12">
                                                <div style={{ width: '75%' }}>
                                                    <Calendar placeholder="Date of Birth" className={"d-none"} value={this.state.dob} onChange={(e) => this.getValues(e.target.value, 5)} style={{ color: 'red' }} showIcon={true} />

                                                </div>
                                                <div>
                                                  <DatePicker selected={this.state.dob} value={this.state.dob} onChange={this.PhandleChange} format={"y-MM-dd"} />
                                                 </div>

                                            </div>
                                            <div className="col-lg-4 col-md-4 col-sm-12">
                                                <input value={this.state.pob} onChange={(e) => this.getValues(e.target.value, 6)} type="text" className="form-control" placeholder="Place of Birth" required="required" />
                                            </div>
                                            <div className="col-lg-4 col-md-4 col-sm-12">
                                                <select value={this.state.gender} onChange={(e) => this.getValues(e.target.value, 7)} className="gender">
                                                <option value="" disabled selected>Gender</option>
                                                    <option value="Male">Male</option>
                                                    <option value="Female">Female</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-lg-4 col-md-4 col-sm-4">
                                                <input value={this.state.email} onChange={(e) => this.getValues(e.target.value, 8)} className="form-control" placeholder="E-mail" />
                                            </div>
                                            <div className="col-lg-4 col-md-4 col-sm-4">
                                                <input value={this.state.password} onChange={this.handlePasswordChange}  type={this.state.hidden ? "password" : "text"} className="form-control" placeholder="Password" />
                                            </div>
                                            <div className="col-lg-1 col-md-1 col-sm-1">
                                            {/* <p className="mt-2" onClick={this.toggleShow} style={{ color: 'gray' }}>show</p> */}
                                           <i className="fa fa-eye mt-3" onClick={this.toggleShow}/>                                                                                    
                                            </div>
                                            <div className="col-lg-2 col-md-2 col-sm-2"  >
                                                   
                                            </div>
                                            <div className="col-lg-2 col-md-2 col-sm-2"  >
                                                    <select name="dropdown" value={this.state.phonecode} onChange={(e) => this.getValues(e.target.value, 13)}>
                                                    <option value="" disabled selected>Phonecode</option>
                                                        {
                                                            this.state.countries.map((data, index) => (
                                                                <option value={data.id}>{data.phonecode}</option>
                                                            ))
                                                         }
                                                    </select>
                                            </div>
                                            <div className="col-3">
                                                <div className="input-group">
                                                <h6 className="text-muted pt-2 " >+</h6>
                                                    <input value={this.state.mob} onChange={(e) => this.getValues(e.target.value, 9)} id="phone" type="tel" className="form-control" placeholder="971 890 890933" />
                                                </div>
                                            </div>
                                            <div className="col-lg-5 col-md-5 col-sm-5">
                                                <select name="dropdown" value={this.state.Cresidence} onChange={(e) => this.getValues(e.target.value, 10)}>
                                                <option value="" disabled selected>Select Country</option>
                                                    {
                                                        this.state.countries.map((data, index) => (
                                                            <option value={data.id}>{data.name}</option>
                                                        ))
                                                     }
                                                </select>
                                            </div>
                                        </div>
                                        <div className="row">
                                            
                                           
                                            <div className="col-lg-7 col-md-7 col-sm-7">
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-12">
                                                <input type="checkbox" value={this.state.termsChecked} onChange={(e) => this.getValues(e.target.checked, 11)} />
                                                <label className=" labelu">By having an account you are agreeing with Enroll's
                                                <a href="" className="pl-1">Terms of Services</a> and <a href="">Privacy Policy</a></label>
                                            </div>
                                        </div>
                                        <div className="row">
                                           
                                            <div className="col-lg-4 col-md-4 col-sm-4 ">
                                                <Link
                                                    to={{
                                                        pathname: this.state.loginpath,
                                                        state: [{ id: 1, name: 'student', color: 'red' }]
                                                    }}>
                                                    <button id="login" onClick={() => this.gotoLogIn()} type="button" style={{ color: 'white' }} className="btn enrollbtn bold-font">Ennroll</button>
                                                </Link>
                                            </div>
                                        </div>
                                        <label className=" labelx">You have an account already?</label>
                                        <div className="row">
                                            <div className="col-lg-4 col-md-4 col-sm-4 ">
                                                
                                                          <Link
                                                        to={{
                                                            pathname: 'login',
                                                            state: [{ id: 1, name: 'student', color: 'red' }]
                                                        }}
                                                    > 
                                                    <button className="btn googlebtn bold-font">login</button>
                                                    </Link>
                                                
                                            </div>

                                        </div>
                                    </form>
                                </div>
                            </div>
                            {/* <!--Reg-Box End--> */}
                        </div>
                        <div className="col-lg-4 col-md-4 d-none col-sm-4 ">
                            <Link
                                to={{
                                    pathname: this.state.navigate,
                                    state:[{id:this.state.id ,name:this.state.visitorName}]
                                }}>

                                <button id="login2" style={{ color: 'white' }} type="button" onClick={() => this.gotoFragment()} className="btn enrollbtn bold-font">Login</button> </Link>
                        </div>
                        <div className="copyright">
                            <h6>©️ Copyright 2019 | Ennroll, All Rights Reserved.</h6>
                        </div>
                    </div>
                    {/* <!--Reg-Bg End--> */}

                </div>

            </div>
        </div>
        );
    }
}