    qualification_get = async () => { //regestration info on login 
        let data={
            stu_id:this.props.location.state[0].id}
         await fetch(URL.url + 'qualification/get_qualification', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ data }),
        })
            .then(res => res.json())
            .then((resjson) => {
                // alert(24);
                 // console.log("sdcsdcdfvdfvdfvsdcdfvdvfgbfvnbfgbfhnfvsgbdfgd");
               var Qualification_name=resjson.data[0].Qualification_name
                var School_clg_center_name=resjson.data[0].School_clg_center_name
                var Grade_percentage_cgpa=resjson.data[0].Grade_percentage_cgpa
                var Select_grade_percentage_cgpa=resjson.data[0].Select_grade_percentage_cgpa
                var Attendance=resjson.data[0].Attendance
                 var Select_grade_percentage_cgpa=resjson.data[0].Select_grade_percentage_cgpa
                  var Did_start_month=resjson.data[0].Did_start_month
                   var Did_start_year=resjson.data[0].Did_start_year
                var Did_finish_month=resjson.data[0].Did_finish_month
                var Did_finish_year=resjson.data[0].Did_finish_year
                var qualificatoin_uri=resjson.data[0].qualificatoin_uri

                console.log(Attendance);

               
                 console.log(Qualification_name);
                var Qualification_name = JSON.parse(Qualification_name);
                var School_clg_center_name = JSON.parse(School_clg_center_name); 
                var Grade_percentage_cgpa = JSON.parse(Grade_percentage_cgpa);
                Attendance = Attendance.replace(/{/g, "[");
                Attendance = Attendance.replace(/}/g, "]");
                var Attendance = JSON.parse(Attendance);
                 var Select_grade_percentage_cgpa = JSON.parse(Select_grade_percentage_cgpa);
                 var Did_start_month = JSON.parse(Did_start_month);
                  var Did_start_year = JSON.parse(Did_start_year);
                   var Did_finish_month = JSON.parse(Did_finish_month);
                    var Did_finish_year = JSON.parse(Did_finish_year);
                var qualificatoin_uri = JSON.parse(qualificatoin_uri);
                 console.log(qualificatoin_uri[0]);
    
                for (var i = 1; i < Qualification_name.length; i++) {
                    // alert('.start_from_m_select_'+Select_grade_percentage_cgpa[i]+"_"+i)
                    $('.Q_add').trigger('click');
                }
                for (var i = 0; i < Qualification_name.length; i++) {
                qualificatoin_uri[i] = qualificatoin_uri[i].replace(/C:\\fakepath\\/i, '')

                    

                    $('.quali_name_'+i).val(Qualification_name[i]);
                    $('.quali_school_name_'+i).val(School_clg_center_name[i]);
                    $('.scoor_val_'+i).val(Grade_percentage_cgpa[i]);
                    $('.attend_'+i).val(Attendance[i]);
                    $('.scoor_select_'+Select_grade_percentage_cgpa[i]+"_"+i).attr('selected',true);
                    $('.start_from_m_select_'+Did_start_year[i]+"_"+i).attr('selected',true);
                    $('.start_from_y_select_'+Did_start_month[i]+"_"+i).attr('selected',true);
                    $('.end_at_m_select_'+Did_finish_month[i]+"_"+i).attr('selected',true);
                    $('.end_at_y_select_'+Did_finish_year[i]+"_"+i).attr('selected',true);
                    $('.Qgraytext_'+i).append("<p class='mt-1' >"+qualificatoin_uri[i]+"</p>")
                }
                
               
                 

            })
            .catch(err => {
                console.log('failed', err)
                // alert('failed')

            })
    }
