    coutries = () => {
        $(".tec").change(function () {
            var selected = $('input[name="countries_box"]:checked').length;
            //  alert();
            //  console.log('in function')
            var maxAllowed = 5;
            if (selected > maxAllowed) {
                $(this).prop("checked", false);
                // alert('limit exceded');
            }

            if ($(this).prop("checked") == true) {
                selected++;
                // alert('add');
                var c_id = $(this).attr('id');
                var c_id = c_id.split('_');
                var c_id = c_id[2];
                $('#' + c_id).addClass('selecteditem');
            } else {
                selected--;
                // alert('remove');
                $(this).prop("checked", false);
                var c_id = $(this).attr('id');
                var c_id = c_id.split('_');
                var c_id = c_id[2];
                $('#' + c_id).removeClass('selecteditem');
            }

        });
    }

