    ChoicesAPI = async () => {
      console.log( this.state.countriesSelected);
      var temp=[];
      var temp2=[];
      $('.selectedcountry').each(function(i,val){

       console.log( $(val).attr('id'));
       temp.push($(val).attr('id'))
       
      });
      // console.log(temp)
      
      $('.selectedintrest').each(function(i,val){
       console.log( $(val).attr('id'));
       temp2.push($(val).attr('id'))
       
      });
      // console.log(temp)
      this.state.countriesSelected=temp;
      this.state.dicipline=temp2;
      this.setState({
        countriesSelected:temp,
        dicipline:temp2,
      });
      // console.log(this.state.aun)
       // console.log( this.state.dicipline);
      // return false;
      this.state.countriesSelected = JSON.stringify(this.state.countriesSelected);
      this.state.dicipline = JSON.stringify(this.state.dicipline);
      this.state.other_countries = JSON.stringify(this.state.other_countries);

      // this.state.countriesSelected,
        let C_data = {
            Stu_profile_id: this.state.studentID,
            stu_application_id: null,//fk
            Other_countries:this.state.other_countries,
            Desire_country:this.state.countriesSelected,           //fk //going hard coded for now
            Require_visa: this.state.visa,   //going hard coded for now
            Why_no: this.state.visareason,
            Desired_level_education: this.state.eduLevel,
            desired_discipline_education: this.state.dicipline,
            Intake_session: this.state.choices_intake,
            Budget: this.state.budget,
            Means_finance: this.state.finacemeanse,

        }

        console.log("Choices in final call API...............", C_data)
        // return false;
        await fetch(URL.url + 'choices/choices_create', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ C_data }),
        })
            .then(res => res.json())
            .then(resjson => {
                // this.doubleClick()
                //   alert(resjson.Message)
            })
            .catch(err => {
                console.log('failed', err)
                // alert('failedddddddd')

            })

    }


