    get_countries = async () => {
        let data = {
        }
        console.log('get countries is calling')
        await fetch(URL.url + 'stu_profile/country_data', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ data }),
        })
            .then(res => res.json())
            .then((resjson) => {
                //   alert(resjson.Message)
                //    console.log(resjson.c_data)
                this.setState({
                  options: resjson.c_data,
                    countriess: resjson.c_data

                })
                $(".loader").removeClass("d-block");
                $(".loader_bg").addClass("d-none");
                $('city'+this.state.countries[0].country_id).select()
                console.log(this.state.countries)


            })
            .catch(err => {
                console.log('failed', err)
               
            })
    }
    onSelect=(selectedList,selectedItem)=> {
        this.state.other_countries=selectedList;
        this.setState({
             other_countries:selectedList
         })
        console.log(this.state.other_countries);
      
    }
onRemove=(selectedList, removedItem)=> {
   this.state.other_countries=selectedList;
    this.setState({
      other_countries:selectedList
    })
    console.log(this.state.other_countries);
}

