//import liraries
import React, { Component } from 'react'
import SplashScreen from './1-SplashScreen'
import LandingPage from './2-LandingPage'
import StudentForm from './4-Student-Reg'
import UniForm from './5-Uni-Reg'
// create a component
export default class fragment extends Component {
    state = {
        screennumber: 1,
        //register1
        value: 1,
        StudyDiscipline: [2],
      
    }

    setData = (k, key) => {
        switch (key) {
            case 1:
                this.setState({
                    Educationlevel: k + 1 //degreeindex: degreeindx
                })
                break;
            case 2:
                //console.log(k)                
                this.setState({
                    Country: k
                })
                break;
          
        }
    }
    selectScreen = (v) => {
        console.log('in select screen :',v)
        this.setState({ screennumber: v })
    }

    Screen = () => {

        // this.props.navigation.navigate('Screenno', { value:1 })  
        if (this.state.screennumber == 1) {
            return (<SplashScreen  value={this.state.value} senddata={(k) => this.setData(k, 1)} Screenno={(v) => this.selectScreen(v)} />)
        }
      else  if (this.state.screennumber == 2) {
            return (<LandingPage  value={this.state.value} senddata={(k) => this.setData(k, 1)} Screenno={(v) => this.selectScreen(v)} />)
        } else if (this.state.screennumber == 3) {
            return (<StudentForm  StudyDiscipline={this.state.StudyDiscipline} senddata={(v) => this.setData(v, 11)} Screenno={(v) => this.selectScreen(v)} />)
        }
        else if (this.state.screennumber == 4) {
            return (<UniForm  StudyDiscipline={this.state.StudyDiscipline} senddata={(v) => this.setData(v, 11)} Screenno={(v) => this.selectScreen(v)} />)
        }
    }
    render() {
        return (

            <div>
                {
                    this.Screen()
                }
            </div>


        );
    }
}



