import React, { Component } from 'react';
import '../main.css';
import {connect} from 'react-redux';
import $ from 'jquery'

class Accreditaion extends Component {
    constructor(props) {
        super(props);
        this.state = {
            validate_temp_acr:'',
        }
    }
    dropAccr = (no_accr) =>{
       

        $('.accr'+no_accr).remove();
        var no_of_Accr_=this.props.myG_AcrData;
        no_of_Accr_--;
        this.props.changeGAcrData(no_of_Accr_);
        
        
    }
   
    render() {
        return (
                <div className={ 'col-lg-4 col-md-12 col-sm-12 d-flex distroy accr'+this.props._Accreditaion }>
                     <input type="text" 
                            className="form-control graytext" 
                            name={"Accreditaion_"+this.props._Accreditaion}
                            placeholder="Professional Accreditaion" 
                            required="required" />
                            <div  >
                            <button onClick={() => this.dropAccr(this.props._Accreditaion)} className="close bg-none text-danger " >&times;</button>
                            </div>
                </div>
        );
    }
}
const mapStateToProps = (state) => {
     return {
         myG_mainData:state.G_mainData,
         myG_RnkData:state.G_RnkData,
         myG_AcrData:state.G_AcrData,
     }
 }

 const mapDispatchToProps = (dispatch) => {
    return {
        changeGMainData: (data) =>{dispatch({ type:'CHANGE_G_MAIN_DATA',payload:data})},
        changeGUniData: (data) =>{dispatch({ type:'CHANGE_UNI_DATA',payload:data})},
        changeGRnkData: (data) =>{dispatch({ type:'CHANGE_RNK_DATA',payload:data})},
        changeGAcrData: (data) =>{dispatch({ type:'CHANGE_ACR_DATA',payload:data})}


    }
 }

export default connect(mapStateToProps,mapDispatchToProps)(Accreditaion);
