import React, { Component, Fragment } from "react";

export default class Example extends Component {
  state = {
    questions: ['program 1'],
    cgpa: ['program 1'],
  }

  handleText = i => e => {
    let questions = [...this.state.questions]
    questions[i] = e.target.value
    this.setState({
      questions
    })
    console.log('Programs:', this.state.questions)
  }

  handleDelete = i => e => {
    e.preventDefault()
    let questions = [
      ...this.state.questions.slice(0, i),
      ...this.state.questions.slice(i + 1)
    ]
    this.setState({
      questions
    })
  }

  addQuestion = e => {
    e.preventDefault()
    let questions = this.state.questions.concat([''])
    this.setState({
      questions
    })
  }

  addQuestiona = e => {
    e.preventDefault()
    let cgpa = this.state.cgpa.concat([''])
    this.setState({
      cgpa
    })
  }

  getValues = i => e => {
    let cgpa = [...this.state.cgpa]
    cgpa[i] = e.target.value
    this.setState({
      cgpa
    })
    console.log('cgpa:', this.state.cgpa)
  }

  render() {
    return (
      <Fragment>
        {this.state.questions.map((question, index) => (
          <span key={index} id="asad">
            <input
              type="text"
              onChange={this.handleText(index)}
              value={question}
            />
            <button onClick={this.handleDelete(index)}>X</button>
          </span>
        ))}

        {this.state.cgpa.map((data, index) => (
          <span key={index}>
          <input
            type="text"
            onChange={this.getValues(index)}
            value={data}
          />
          </span>
        ))}
        <button onClick={(e) => [this.addQuestion(e), this.addQuestiona(e)]}>Add New Question</button>
      </Fragment>
    )
  }
}