import React, { Component } from 'react';
import Program from './Program';
import Criteria from './Criteria';
import Levels from './Levels';
import '../main.css';
import $ from 'jquery';
import {connect} from 'react-redux';


class Department extends Component {
    constructor(props) {
        super(props);
        this.state = {
            no_of_prog: [],
            no_p: 0,
            _mainData:[],
            _temp_departements:0,
            _temp_Programs:0,
        }
         

        this.state.no_of_prog.push(
                <div >
                   <div className="row">
                       <div className="col-lg-4 col-md-12 col-sm-12 displayinline">
                           <p className="pr-2">Programs Offered:<p>(Under these Departments)</p></p>
                       </div>
                       <div className="col-5">
                       </div>
                       <div className="col-lg-3 col-md-12 col-sm-12">
                          <button onClick={ (e) => this.appendProgram() } className={"btn addbtn add_hide append_program_btn_0_"+this.props.no_d}>
                              <i className="fa fa-plus pr-2"></i>
                              Add Program
                          </button>
                       </div>
                   </div>
                   <div className="row  ">
                       <div id="program13" className="col-lg-3 col-md-12 col-sm-12 displayinline">
                           <input type="text" name={"prog_"+this.props.no_f+"_"+this.props.no_d+"_"+this.state.no_p} className="form-control graytext" placeholder="Option 1" required="required" />
                           <span className="must3">*</span>
                       </div>
                   </div>
                   <Criteria no_f={this.props.no_f} no_d={this.props.no_d}  no_p={this.state.no_p}/>
                   <Levels no_f={this.props.no_f} no_d={this.props.no_d}  no_p={this.state.no_p}/>
                </div>);
        

        this.setState({ 
          no_of_prog : this.state.no_of_prog, 
        });
        this.props.myG_mainData[this.props.no_f].push({d:this.props.no_d,p:this.state.no_p});

        this.props.changeGMainData(this.props.myG_mainData)

        console.log('d:'+this.props.no_d+',p:'+this.state.no_p );

        console.log(this.props.no_f);

    }
    componentDidMount = () => {
          $('.first_drop0').hide();
         }
    
    appendProgram = (e) => {

      // alert(123);
      if(this.props. G_p_flag == 1){
        this.props.changeG_P_Data(2);
        this.state.no_p =0 ;
      }
          // alert(this.props. G_p_flag);

        // alert(this.state.no_p);

        this.state.no_p++;
        console.log(this.props.myG_mainData[this.props.no_f][this.props.no_d]);

        this.props.myG_mainData[this.props.no_f][this.props.no_d].p=this.state.no_p;
        // console.log(this.props.myG_mainData);
        this.props.changeGMainData(this.props.myG_mainData)
        console.log('d:'+this.props.no_d+',p:'+this.state.no_p );
        this.state.no_of_prog.push(
          <div className={"edit_pro_del"} >
            <Program no_f={this.props.no_f} no_d={this.props.no_d}  no_p={this.state.no_p}/>
            <Criteria no_f={this.props.no_f} no_d={this.props.no_d}  no_p={this.state.no_p}/>
            <Levels no_f={this.props.no_f} no_d={this.props.no_d}  no_p={this.state.no_p}/>
          </div>
        );

        this.setState({
                    no_of_prog : this.state.no_of_prog,
                    no_p:  this.state.no_p,
                    _mainData:this.state._mainData,
                 });
    }
    dropDepartment = (f,d) => {
          // alert(f+'_'+d);
          $('.department_class_'+f+'_'+d+'').remove();
    }

    validateNumber = (max,name) => {
      let inp= $('input[name="'+name+'"]').val();
      let l=inp.length-1;
      if( inp.codePointAt(l) >= 48 && inp.codePointAt(l) <= 57 && inp <= max && inp >= 0 ){

      }else{
        $('input[name="'+name+'"]').val('');
      }
    }

    validateAlphabet = (name) => {
      let inp= $('input[name="'+name+'"]').val();
      let l=inp.length-1;
      if( inp.codePointAt(l) >= 65 && inp.codePointAt(l) <= 90 || inp.codePointAt(l) >= 97 && inp.codePointAt(l) <= 122 ){

      }else{
        $('input[name="'+name+'"]').val('');
      }
    }
    render() {
        return (
                <div className={"_dropable_"+this.props.no_d+" department_class_"+this.props.no_f+"_"+this.props.no_d}>
                    <hr/>
                   {/* <button onClick={ ()=>{this.props.changeGMainData(this.props.myG_mainData)} }>Change now!</button>*/}
                    <div>
                        <div className="row">
                            <div className="col-lg-4 col-md-12 col-sm-12 displayinline">
                            </div>
                            <div className="col-5">
                            </div>
                            <div className="col-lg-3 col-md-12 col-sm-12">
                                <button className={"btn addbtn add_hide  first_drop"+this.props.no_d} onClick={ (e) => this.dropDepartment(this.props.no_f,this.props.no_d) }><i clsName="fa fa-minus pr-2"></i>Drop This Department </button>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-lg-3 col-md-12 col-sm-12 displayinline">
                                <input type="text" className="form-control graytext" name={"dept_"+this.props.no_f+"_"+this.props.no_d}  placeholder="Enter Department" required="required" />
                                <span className="must3">*</span>
                            </div>
                        </div>
                        <div className={"dropable_programs program_class_"+this.props.no_f+"_"+this.props.no_d+"_"+this.props.no_p}>
                            <div>{this.state.no_of_prog}</div>
                        </div>
                    </div>
                </div>
            );
    }
}

const mapStateToProps = (state) => {
     return {
        myG_mainData:state.G_mainData,
         G_p_flag:state.G_p_flag,

     }
 }

 const mapDispatchToProps = (dispatch) => {
    return {
        changeGMainData: (data) =>{dispatch({ type:'CHANGE_G_MAIN_DATA',payload:data})},
        changeG_P_Data: (data) =>{dispatch({ type:'CHANGE_P_Flag',payload:data})},
    }
 }

export default connect(mapStateToProps,mapDispatchToProps)(Department);
