import React, { Component } from 'react';
import Department from './Department'
import Program from './Program'
import Criteria from './Criteria'
import Levels from './Levels'
import '../main.css';
import $ from 'jquery'

class Department_init extends Component {
    constructor(props) {
        super(props);
        this.state = {
            no_of_prog: [],
            no_of_dept:[],
            no_p: 0,
            no_d: 0,
        }

        this.state.no_of_prog.push(
          <div>
            <div>
               <div className="row">
                   <div className="col-lg-4 col-md-12 col-sm-12 displayinline">
                       <p className="pr-2">Programs Offered:<p>(Under these Departments)</p></p>
                   </div>
                   <div className="col-5">
                   </div>
                   <div className="col-lg-3 col-md-12 col-sm-12">
                       <button onClick={ (e) => this.appendProgram() } className="btn addbtn"><i className="fa fa-plus pr-2"></i>Add Program</button>
                   </div>
               </div>
               <div className="row">
                   <div className="col-lg-3 col-md-12 col-sm-12 displayinline">
                       <input type="text" className="form-control graytext" name="prog_0_0" placeholder="Option 1" required="required" />
                       <span className="must3">*</span>
                   </div>
               </div>
           </div>
           <Criteria no_d={0}  no_p={this.state.no_p}/>
           <Levels no_d={0}  no_p={this.state.no_p}/>
        </div>);
        this.setState({ no_of_prog : this.state.no_of_prog });
    }

    appendProgram = (e) => {
        // alert('append program init');
        this.state.no_p++;
        this.state.no_of_prog.push(
            <div>
              <Program no_d={this.state.no_d}  no_p={this.state.no_p}/>
              <Criteria no_d={this.state.no_d}  no_p={this.state.no_p}/>
              <Levels no_d={this.state.no_d}  no_p={this.state.no_p}/>
            </div>
        );
        this.setState({
                    no_of_prog : this.state.no_of_prog,
                    no_p:  this.state.no_p,
                 });
    }

    appendDepartment = (e) => {

        this.state.no_d++;
        this.state.no_of_dept.push(<Department no_d={this.state.no_d}/>);
        this.setState({
                    no_of_dept : this.state.no_of_dept,
                    no_d:  this.state.no_d,
                 });
    }

    componentDidMount = () => {
        // this.appendProgram();
    }
    render() {
        return (
                <div>
                    <div className="department_class_0">
                        <div>
                            <div className="row">
                                <div className="col-lg-4 col-md-12 col-sm-12 displayinline">
                                </div>
                                <div className="col-5">
                                </div>
                                <div className="col-lg-3 col-md-12 col-sm-12">
                                    <button className="btn addbtn" onClick={ (e) => this.appendDepartment() }><i className="fa fa-plus pr-2"></i>Add Department</button>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-lg-3 col-md-12 col-sm-12 displayinline">
                                    <input type="text" className="form-control graytext"  name="dept0"  placeholder="Enter Department" required="required" />
                                    <span className="must3">*</span>
                                </div>
                            </div>   
                            <div>{this.state.no_of_prog}</div>
                        </div>
                    </div>
                    <div>{this.state.no_of_dept}</div>
                </div>
            );
    }
}

export default Department_init;