import React, { Component } from 'react';
import Department from './Department';
import Department_init from './Department_init';
import {connect} from 'react-redux';
import '../main.css';
import $ from 'jquery'


class Faculty extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ////tab1
            no_of_dept: [],
            no_d:-1,
        }
        this.appendDepartment();
        
    }

    appendDepartment = () => {
      if(this.props. G_d_flag == 1){
        this.props.changeG_D_Data(2);
        this.state.no_d =0 ;
      }

        this.state.no_d++;
        this.state.no_of_dept.push(<Department no_f={this.props.no_f} no_d={this.state.no_d}/>);
        this.setState({
                    no_of_dept : this.state.no_of_dept,
                    no_d:  this.state.no_d,
                 });
    }

    render() {
        return (
               <div className="col-12 rounded mt-3">
                   <div className="row">
                       <div className="col-lg-4 col-md-12 col-sm-12 displayinline">
                           <p className="">Faculty:</p>
                           <input type="text"
                                className="form-control graytext ml-2" name={"faculity_"+this.props.no_f} placeholder="Type here..." required="required" />
                           <span className="must3 pr-2">*</span>
                       </div>
                       <div className=" col-5">
                       </div>
                       <div className=" col-3">
                          <button className="btn addbtn append_department_btn edit_hide " onClick={ () => this.appendDepartment() }><i className="fa fa-plus pr-2"></i>Add Department</button>
                       </div>
                   </div>
                   <div>{this.state.no_of_dept}</div>
               </div>
            );
    }
}


const mapStateToProps = (state) => {
     return {
         myG_mainData:state.G_mainData,
         G_UniData:state.G_UniData,
         G_d_flag:state.G_d_flag,
     }
 }
 const mapDispatchToProps = (dispatch) => {
    return {
        changeGMainData: (data) =>{dispatch({ type:'CHANGE_G_MAIN_DATA',payload:data})},
        changeG_D_Data: (data) =>{dispatch({ type:'CHANGE_D_Flag',payload:data})},
    }
 }

export default connect(mapStateToProps,mapDispatchToProps)(Faculty);
