    getValuesContact = (v, no) => {
        const name = /^[a-zA-Z ]+$/
        const number = /^[0-9.]+$/
        const email = /^[a-z0-9@._-]+$/
        switch (no) {
            case 1:
                if (email.test(v)) {
                    this.setState({
                        email: v
                    })
                }
                else if (v.length == 0) {
                    this.setState({
                        email: ''
                    })
                }
                console.log('email:', this.state.email)
                break;
            case 2:
                if (number.test(v)) {
                    this.setState({
                        mob: v
                    })
                }
                else if (v.length == 0) {
                    this.setState({
                        mob: ''
                    })
                }
                console.log('mobile:', this.state.mob)
                break;
            case 3:
                this.setState({
                    address: v
                })
                console.log('address:', this.state.address)
                break;
            case 4:
                this.setState({
                    otheraddress: v
                })
                console.log('otheraddress:', this.state.otheraddress)
                break;
            case 5:
                this.setState({
                    fblink: v
                })
                console.log('fblink:', this.state.fblink)
                break;
            case 6:
                this.setState({
                    instalink: v
                })
                console.log('instalink:', this.state.instalink)
                break;
            case 7:
                this.setState({
                    linkedinlink: v
                })
                console.log('linkedinlink:', this.state.linkedinlink)
                break;
        }
    }
